@use "../utils/mixins"as*;
@use "../utils/functions/"as*;

.checks {
  &__item {
    position: relative;
    padding-left: 30px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 4px;
      width: 20px;
      height: 20px;
      background-image: url("../icons/check.svg");
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }

    &:not(:last-child) {
      margin-bottom: 22px;

      @include media(mobile-l) {
        margin-bottom: 16px;
      }
    }
  }
}