@use "../utils/mixins"as*;
@use "../utils/functions/"as*;

h1 {
  @include font-size(56, 73);

  font-weight: 600;
  text-align: center;

  @include media(mobile-l) {
    @include font-size(36, 47);
  }
}

.title {
  @include font-size(46, 60);

  font-weight: 600;
  letter-spacing: 0.01em;
  text-align: center;

  @include media(tablet) {
    @include font-size(32, 42);
  }

  &--middle {
    @include font-size(40, 52);

    @include media(tablet) {
      @include font-size(32, 52);

      letter-spacing: 0.01em;
    }
  }

  &--small {
    @include font-size(32, 42);

    @include media(tablet) {
      @include font-size(24, 36);
    }
  }
}

.text {
  font-size: 18px;
  line-height: 1.5;
  letter-spacing: 0.01em;
  color: var(--color-gray-400);

  @include media(tablet) {
    font-size: 16px;
    line-height: 1.62;
  }
}