@use "../utils/mixins"as*;
@use "../utils/functions/"as*;

.choose {
  background-color: var(--color-green-50);

  &__title {
    margin-bottom: rem(43);
    padding-top: 5px;

    @include media(mobile-m) {
      margin-bottom: 42px;
      padding-top: 0;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: rem(30);

    @include media(mobile-l) {
      grid-template-columns: 1fr;
      gap: 25px;
    }
  }

  &__item {
    border-radius: var(--radius-main);
    padding: rem(45) rem(44) rem(38);
    background-color: var(--color-white);

    &:last-child {
      .choose__image {
        max-width: 80px;

        @include media(mobile-l) {
          margin-bottom: 16px;
          max-width: 60px;
        }
      }
    }

    @include media(mobile-m) {
      padding: 33px 17px;
    }
  }

  &__image {
    margin: 0 auto rem(25);

    @include media(mobile-l) {
      max-width: 60px;
    }

    @include media(mobile-m) {
      margin-bottom: 22px;
    }
  }

  &__item-title {
    margin-bottom: rem(25);

    @include media(mobile-m) {
      margin-bottom: 17px;
    }
  }

  &__text {
    line-height: 1.6;
    letter-spacing: 0.007em;
  }
}