@use "../utils/mixins"as*;
@use "../utils/functions/"as*;

.personal {
  &__title {
    margin-bottom: rem(43);

    @include media(mobile-l) {
      margin-bottom: 45px;
    }
  }

  &__item {
    counter-increment: personal;
    position: relative;
    display: grid;
    align-items: center;
    grid-template-columns: repeat(2, 1fr);
    padding-bottom: rem(50);
    gap: 0 80px;

    &::before {
      @include flex-all-center;

      content: counter(personal);
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: 1;
      border-radius: 50%;
      width: 64px;
      height: 65px;
      font-weight: 600;
      font-size: 20px;
      line-height: 1.4;
      background-color: var(--color-gray-100);
      transform: translate(-50%, -80%);
      transition: all 0.2s;

      @include media(mobile-l) {
        display: none;
      }
    }

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      bottom: 0;
      z-index: 0;
      width: 4px;
      height: 100%;
      background-color: var(--color-gray-100);
      transform: translateX(-50%);
      transition: all 0.2s;

      @include media(mobile-l) {
        display: none;
      }
    }

    &:not(:last-child) {
      @include media(mobile-l) {
        margin-bottom: 44px;
      }
    }

    &:first-child {
      padding-top: 0;

      &::after {
        height: 50%;
      }
    }

    &:last-child {
      padding-top: rem(50);
      padding-bottom: 0;

      &::before {
        transform: translate(-50%, -10%);
      }

      &::after {
        top: 0;
        bottom: auto;
        height: 50%;
      }

      @include media(mobile-l) {
        padding: 0;
      }
    }

    &--reverse {
      padding-bottom: 0;

      &::before {
        transform: translate(-50%, -50%);
      }

      .personal__visual {
        order: 2;
        margin-left: auto;

        @include media(mobile-l) {
          order: inherit;
          margin: 0 auto;
        }
      }

      .personal__content {
        margin-right: auto;
        margin-left: 0;

        @include media(mobile-l) {
          margin: 0 auto;
        }
      }
    }

    &.active {

      &::before,
      &::after {
        color: var(--color-white);
        background-color: var(--color-green-300);
      }
    }

    @include media(mobile-l) {
      justify-items: center;
      grid-template-columns: 1fr;
      margin: 0 auto;
      padding: 0;
      text-align: center;
      gap: 25px 0;
    }
  }

  &__visual {
    @include flex-all-center;

    border-radius: var(--radius-main);
    padding: rem(48) rem(55) 55px;
    width: 100%;
    height: 100%;
    max-width: 500px;
    background-color: var(--color-green-50);

    &--account {
      padding: 47px 40px;

      img {
        margin: 0 auto;
        width: 100%;
      }
    }

    @include media(tablet-wide) {
      padding: 20px 29px 43px;
    }

    &--request {
      align-items: flex-end;
      padding-bottom: 0;

      @include media(tablet-wide) {
        padding-bottom: 0;
      }
    }
  }

  &__content {
    margin-right: 0;
    margin-left: auto;
    width: 100%;
    max-width: 502px;

    .text {
      @include media(mobile-l) {
        line-height: 1.7;
      }
    }

    @include media(mobile-l) {
      margin: 0 auto;
      text-align: center;
    }
  }

  &__content-title {
    margin-bottom: 14px;
    text-align: left;

    @include media(mobile-l) {
      @include font-size(26, 36);

      text-align: center;
    }
  }

  @include media(mobile-l) {
    gap: 40px 0;
  }
}