@use "../utils/mixins"as*;
@use "../utils/functions/"as*;

.details {
  &__inner {
    display: grid;
    align-items: center;
    grid-template-columns: 0.8fr 1fr;
    margin-bottom: rem(48);
    gap: 25px;

    @include media(mobile-l) {
      justify-items: center;
      grid-template-columns: 1fr;
      margin-bottom: 40px;
      gap: 40px;
    }
  }

  &__visual {
    border-radius: var(--radius-main);
    padding: 55px 0;
    width: 100%;
    max-width: 480px;
    background-color: var(--color-green-50);

    @include media(mobile-l) {
      order: 2;
      padding: 30px 0;
      max-width: 320px;
    }

    @include media(mobile-s) {
      padding: 20px 0;
    }
  }

  &__title {
    margin-bottom: 27px;
    letter-spacing: 0.03em;
    text-align: left;

    @include media(mobile-l) {
      line-height: 1.3;
      letter-spacing: 0.01em;
      text-align: center;
    }
  }

  &__content {
    padding-top: 5px;

    @include media(mobile-l) {
      order: 1;
      margin: 0 auto;
      max-width: 500px;
    }
  }

  &__text {
    margin-bottom: 27px;

    @include media(mobile-l) {
      margin-bottom: 20px;
      line-height: 1.7;
    }
  }

  &__message {
    border-radius: var(--radius-main);
    padding: 24px;
    font-size: 16px;
    background-color: var(--color-yellow-300);

    @include media(mobile-l) {
      line-height: 1.62;
    }
  }

  .checks {
    @include media(mobile-l) {
      margin: 0 auto;
      max-width: 300px;
    }
  }
}