@use "../utils/functions/"as*;
@use "../utils/mixins/"as*;

.site-container {
  overflow-x: clip;
}

.hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.sr-only {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
}

.section {
  padding: rem(111) 0;

  @include media(mobile-m) {
    padding: 64px 0;
  }
}