@use "../utils/mixins"as*;
@use "../utils/functions/"as*;

.footer {
  padding: rem(45) 0;

  &__list {
    @include flex-v-center;

    justify-content: center;

    @include media(mobile-m) {
      flex-direction: column;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: rem(45);

      @include media(mobile-m) {
        margin-right: 0;
        margin-bottom: 16px;
      }
    }
  }

  &__link {
    position: relative;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.6;
    letter-spacing: 0.01em;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 3px;
      margin: 0 auto;
      width: 0;
      height: 1px;
      background-color: var(--color-dark-400);
      transition: width 0.2s;
    }

    &:focus,
    &:hover {
      &::before {
        width: 100%;
      }
    }
  }
}