@use "../utils/functions/"as*;
@use "../utils/mixins"as*;

.page {
  font-family: var(--font-family);
  font-weight: 400;
  font-style: normal;
  font-size: 1vw;
  line-height: 1.6;
  letter-spacing: 0.01em;
  color: var(--color-dark-400);
  background-color: var(--color-white);

  &__body {
    min-width: 320px;
    min-height: 100%;
  }
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
}