:root {
  // base
  --font-family: 'poppins', sans-serif;
  --content-width: 1180px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --color-white: #fff;
  --color-dark-400: #222;
  --color-gray-100: #f6f6f6;
  --color-gray-400: #333;
  --color-yellow-300: rgba(255, 214, 44, 0.2);
  --color-yellow-400: #ffd62c;
  --color-yellow-500: #eac015;
  --color-green-50: #e7faf0;
  --color-green-300: #88e16f;
  --color-green-400: #0fc15d;
  --hero-gradient: radial-gradient(99.07% 99.07% at 49.2% 48.36%, #c9fde2 0%, #ffffff 51.56%, #ffffff 100%);

  // radius
  --radius-main: 24px;
}