@use "../utils/mixins"as*;
@use "../utils/functions/"as*;

.sure {
  &__inner {
    border-radius: var(--radius-main);
    padding: rem(70);
    background-color: var(--color-green-50);

    @include media(mobile-m) {
      padding: 40px 16px;
    }
  }

  &__title {
    margin-bottom: rem(30);
    letter-spacing: 0.006em;
    text-align: left;

    @include media(mobile-l) {
      margin-bottom: 25px;
      line-height: 1.3;
      text-align: center;
    }
  }

  &__text {
    margin-bottom: rem(48);
    width: 100%;
    max-width: 790px;
    font-size: 16px;
    letter-spacing: 0;

    @include media(mobile-l) {
      text-align: center;
    }

    @include media(mobile-m) {
      margin-bottom: 42px;
      line-height: 1.6;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: rem(28) rem(28);

    @include media(mobile-l) {
      grid-template-columns: 1fr;
      gap: 26px 0;
    }
  }

  &__item {
    @include flex-v-center;

    position: relative;
    border-radius: var(--radius-main);
    padding: 30px 50px 30px 92px;
    background-color: var(--color-white);

    &::before {
      content: "";
      position: absolute;
      left: 32px;
      top: 50%;
      width: 40px;
      height: 40px;
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      transform: translateY(-50%);

      @include media(mobile-s) {
        left: 0;
        right: 0;
        top: 24px;
        margin: 0 auto;
        width: 60px;
        height: 60px;
        transform: translate(0);
      }
    }

    &--age {
      &::before {
        background-image: url("../icons/age-limit.svg");
      }
    }

    &--house {
      &::before {
        background-image: url("../icons/house.svg");
      }
    }

    &--income {
      &::before {
        background-image: url("../icons/income.svg");
      }
    }

    &--security {
      &::before {
        background-image: url("../icons/data-security.svg");
      }
    }

    @include media(mobile-s) {
      padding: 95px 30px 23px;
      text-align: center;
    }
  }

  &__item-title {
    font-weight: 500;
    font-size: 20px;
    line-height: 1.25;
    letter-spacing: 0;

    @include media(mobile-s) {
      font-size: 18px;
      line-height: 1.5;
    }
  }
}