@use "../utils/mixins"as*;
@use "../utils/functions/"as*;

.banner {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: rem(66);
  border-radius: var(--radius-main);
  padding: 24px 15px 0;
  background-image: url("../images/section.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--color-green-50);
  gap: 20px;

  &__content {
    padding: rem(37) 0 rem(27) rem(72);

    @include media(mobile-l) {
      padding: 15px 0;
    }
  }

  &__title {
    margin-bottom: rem(34);
    width: 100%;
    max-width: 570px;
    text-align: left;

    @include media(mobile-m) {
      margin-bottom: 35px;
      text-align: center;
    }
  }

  &__image {
    margin: 0 auto;
    width: 100%;
    height: 355px;
    max-width: 370px;
    object-fit: contain;

    @include media(tablet) {
      position: absolute;
      right: 0;
      bottom: 0;
      max-width: 300px;
      height: auto;
    }

    @include media(mobile-l) {
      max-width: 250px;
    }

    @include media(mobile-m) {
      position: relative;
    }
  }

  &__btn {
    @include media(mobile-m) {
      margin: 0 auto;
    }
  }

  @include media(mobile-l) {
    grid-template-columns: 1fr 0.7fr;
  }

  @include media(mobile-m) {
    grid-template-columns: 1fr;
    margin-bottom: 57px;
  }
}